import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectFriends} from '../../store/reducers';
import {frendsActions} from '../../store/actions';

import {Panel} from '@vkontakte/vkui';

import UsersList from '../UsersList';

const Friends = ({id, go, users, dispatch}) => {
	const {updateFriends, fetchedFriends} = frendsActions;
	let offsetUsers = 0;

	const handleScroll = () => {
		offsetUsers += 100;
		dispatch(updateFriends(offsetUsers));
	};

	const handleSearch = (search) => {
		return users.filter(({first_name, last_name, status, domain}) => {
			const q = `${first_name} ${last_name} ${domain} ${status}`.toLowerCase();
			return q.indexOf(search.toLowerCase()) > -1;
		});
	};

	const handleFetched = (setFetching) => {
		dispatch(fetchedFriends(setFetching));
	}

	return(
		<Panel id={id}>
			<UsersList
				go={go}
				usersList={users}
				handleScrollBottom={handleScroll}
				handleFetched={handleFetched}
				searchFilter={handleSearch}
				isSearch={true}
				headerPanelTitle='Друзья'
				footerNameCount='друзей'
			/>
		</Panel>
	);
}

Friends.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	users: selectFriends.getFriends(state)
});
export default connect(mapStateToProps, null)(Friends);