import React from 'react';
import PropTypes from 'prop-types';
import {Cell,Avatar} from '@vkontakte/vkui';

const INSTAGRAM = 'instagram';
const VK = 'vk';

const connectionsType = ({id, type, instagram}) => {
	switch(type) {
		case VK: return `https://vk.com/id${id}`;
		case INSTAGRAM: return `https://www.instagram.com/${instagram}`;
		default: return `https://vk.com/id${id}`;
	}
};

const openUrl = (url) => {
	window.location.href = url;
};

const  User = ({
	size,
	id,
	photo_50,
	photo_100,
	photo_200,
	photo_200_orig,
	city,
	country,
	first_name,
	last_name,
	bdate,
	description,
	instagram,
	connections
}) => {
	const url = connectionsType({ id, type: connections, instagram });
	return (
		<Cell
			key={id}
			before={<Avatar
					size={size}
					src={photo_200_orig || photo_200 || photo_100 || photo_50}
				/>}
			size="l"
			description={description || `${city.title} ${country.title}`}
			onClick={() => openUrl(url)}
		>
			{`${first_name} ${last_name}`}
		</Cell>
	);
}

User.propTypes = {
	id: PropTypes.number,
	size: PropTypes.number,
	photo_50: PropTypes.string,
	photo_100: PropTypes.string,
	photo_200: PropTypes.string,
	photo_200_orig: PropTypes.string,
	first_name: PropTypes.string,
	last_name: PropTypes.string,
	bdate: PropTypes.string,
	description: PropTypes.string,
	connections: PropTypes.string,
	instagram: PropTypes.string,
	city: PropTypes.shape({title: PropTypes.string}),
	country: PropTypes.shape({title: PropTypes.string})
};

User.defaultProps = {
	size: 48,
	city: {title: ''},
	country: {title: ''},
	connections: 'vk'
};

export default User;