import {
	FETCHED_FRIENDS,
	UPDATE_FRIENDS,
	UPDATE_INSTAGRAM_FRIENDS,
	UPDATE_BANNED_FRIENDS
} from './actionType';

const userInitialState = {
	users: [],
	instagramUsers: [],
	bannedUsers: []
};


export default function friends (state = userInitialState, action = {}) {
	switch (action.type) {
		case FETCHED_FRIENDS:
			return {
				...state,
				users: [...action.users],
				instagramUsers: [...action.users.filter(user => user.instagram)],
				bannedUsers: [...action.users.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned')]
			};
		case UPDATE_FRIENDS:
			return {
				...state,
				users: [...state.users, ...action.users],
			};
		case UPDATE_INSTAGRAM_FRIENDS:
			return {
				...state,
				instagramUsers: [...state.instagramUsers, ...action.users.filter(user => user.instagram)],
			};
		case UPDATE_BANNED_FRIENDS:
			return {
				...state,
				bannedUsers: [...state.bannedUsers, ...action.users.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned')]
			};
		default:
			return state;
	};
};

export const getFriends = (state) => {
	return state.friends.users;
};

export const getFriendsInInstagram = (state) => {
	return state.friends.instagramUsers;
};

export const getBannedFriends = (state) => {
	return state.friends.bannedUsers;
};