import React, {useState} from 'react';

import {View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './containers/Home';
import Friends from './containers/Friends/Friends';
import Instagram from './containers/Friends/Instagram';
import BannedFriends from './containers/Friends/BannedFriends';

const App = (props) => {
	const [activePanel, setActivePanel] = useState('home');
	const go = (e) => setActivePanel(e.currentTarget.dataset.to);

	return (
		<View activePanel={activePanel}>
			<Home id="home" go={go} />
			<Friends id="friends" go={go} />
			<Instagram id="friendsInInstagram" go={go} />
			<BannedFriends id="deletedAndBanned" go={go} />
		</View>
	);
};

export default App;