import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';

import {Search} from '@vkontakte/vkui';

import User from '../components/User';
import RefreshList from '../components/RefreshList';
import HeaderPanel from '../components/HeaderPanel';

const UsersList = ({
	go,
	usersList,
	handleScrollBottom,
	handleFetched,

	searchFilter,
	isSearch,

	headerPanelTitle,
	footerNameCount,

	...otherProps
}) => {
	const [isFetching, setIsFetching] = useState(false);
	const [search, setSearch] = useState('');
	const [userSearch, setUserSearch] = useState('');

	useEffect(() => {
		window.addEventListener('scroll', handleScroll);
		handleFetched(setFetching);
	}, []);

	const onRefresh = () => {
		setIsFetching(true);
		handleFetched(setFetching);
	};

	const setFetching = () => setIsFetching(false);

	const handleScroll = (e) => {
		const scrollHeight = e.target.documentElement.scrollHeight;
		const clientHeight = e.target.documentElement.clientHeight;
		const scrollY = window.pageYOffset + 5;
		if (scrollY >= scrollHeight - clientHeight) {
			handleScrollBottom();
		}
	};

	const handleSearch = (search) => {
		const searchUsers = searchFilter(search);
		setSearch(search);
		setUserSearch(searchUsers);
	};

	const handelCloseSearch = () => {
		setSearch('');
		setUserSearch('');
	};

	return(
		<>
			<HeaderPanel title={headerPanelTitle} handelClick={go} dataTo='home' />
			{usersList.length > 0 && isSearch && <Search value={search} onChange={handleSearch} onClose={handelCloseSearch}/>}
			<RefreshList
				handleScroll={handleScroll}
				handleRefresh={onRefresh}
				isFetching={isFetching}
				list={!userSearch.length ? usersList : userSearch}
				footerNameCount={footerNameCount}
			>
				<User size={36} go={go} {...otherProps} />
			</RefreshList>
		</>
	);
}

UsersList.propTypes = {
	go: PropTypes.func.isRequired,
	usersList: PropTypes.array.isRequired,
	handleScrollBottom: PropTypes.func.isRequired,
	handleFetched: PropTypes.func.isRequired,

	searchFilter: PropTypes.func,
	isSearch: PropTypes.bool,

	headerPanelTitle: PropTypes.string,
	footerNameCount: PropTypes.string,
};

UsersList.defaultProps = {
	isSearch: false,
	searchFilter: () => {},
	headerPanelTitle: '',
	footerNameCount: ''
};

export default UsersList;